.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.unlist{
    list-style: none;
}

.inputText{
    width:10cm;
}

.list{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm;
    border: thin solid rgb(197, 197, 197);
    border-radius: 6px;

}

.items{
    font-size: 16px;
    margin:0.5cm
}
.delete{
    width:1cm;
    height:1cm;
}